"use client";

import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { useRouter } from 'next/navigation';

import { View, Document, DocumentStatus } from '../../lib/types';
import { Input, Select, SelectItem, RadioGroup, Radio, Button, Card, Spinner } from "@nextui-org/react";
import { UploadCloud, FileWarning } from 'lucide-react';
// import * as pdfjs from 'pdfjs-dist'; // Remove direct import

import OldSpinner from '../../components/Spinner';
import { useAppContext } from '../../lib/AppContext';

// Set the worker source for pdfjs-dist (will be set after dynamic import)
// pdfjs.GlobalWorkerOptions.workerSrc = '/pdf.worker.mjs'; // Remove direct setting

const DOCUMENT_TYPES = [
  'Fact Sheet',
  'White Paper',
  'Presentation',
  'Report',
  'Brochure',
  'Other',
];

const PRODUCTION_FREQUENCIES = [
  'Daily',
  'Weekly',
  'Monthly',
  'Quarterly',
  'Annually',
  'Ad-hoc',
];

const TARGET_AUDIENCES = [
  'Retail',
  'Institutional',
  'Advisors',
  'Internal',
  'High Net Worth',
];

const FUND_TYPES = [
  'Open-ended Fund',
  'Close-ended Fund',
  'ETF',
  'Hedge Fund',
  'Private Equity',
  'Real Estate Fund',
];

const UploadPage: React.FC<{}> = () => {

  const { onUploadComplete } = useAppContext();
  const router = useRouter();

  const [projectName, setProjectName] = useState('New Marketing Document');

  const [documentType, setDocumentType] = useState('Fact Sheet');

  const [title, setTitle] = useState('');

  const [file, setFile] = useState<File | null>(null);

  const [isLoading, setIsLoading] = useState(false);

  const [error, setError] = useState<string | null>(null);



  // State for new fields from original spec

  const [firstUseDate, setFirstUseDate] = useState(new Date().toISOString().split('T')[0]);

  const [asOfContentDate, setAsOfContentDate] = useState(new Date().toISOString().split('T')[0]);

  const [productionFrequency, setProductionFrequency] = useState('Monthly');

  const [targetAudience, setTargetAudience] = useState('Retail');

  const [isProductMentioned, setIsProductMentioned] = useState(true);

  const [fundType, setFundType] = useState('Open-ended Fund');

  const [pdfjsLib, setPdfjsLib] = useState<any>(null);

  useEffect(() => {
    // Dynamically import pdfjs-dist on the client-side
    const loadPdfjs = async () => {
      const pdfjs = await import('pdfjs-dist');
      pdfjs.GlobalWorkerOptions.workerSrc = '/pdf.worker.mjs';
      setPdfjsLib(pdfjs);
    };
    loadPdfjs();
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {

    e.preventDefault();

    if (!file) {

      setError("Please select a document to upload.");

      return;

    }

    if (!title) {

        setTitle(file.name.replace(/\.[^/.]+$/, ""));

    }

    setIsLoading(true);

    setError(null);



    try {
        if (!pdfjsLib) {
          setError("PDF.js library not loaded. Please try again.");
          setIsLoading(false);
          return;
        }

        const arrayBuffer = await file.arrayBuffer();
        const pdf = await pdfjsLib.getDocument({ data: arrayBuffer }).promise;
        let extractedText = '';
        for (let i = 1; i <= pdf.numPages; i++) {
          const page = await pdf.getPage(i);
          const textContent = await page.getTextContent();
          const pageText = textContent.items.map((item: any) => item.str).join(' ');
          extractedText += `--- PAGE ${i} ---\n${pageText}\n`;
        }

        const newDocId = `doc-${Date.now()}`;

        const newDoc: Document = {

          id: newDocId,

          projectName,

          title: title || file.name.replace(/\.[^/.]+$/, ""),

          type: documentType,

          status: DocumentStatus.InReview,

          lastUpdated: new Date().toISOString().split('T')[0],

        };



        // Simulate a delay for a better UX, then complete with mock data

        setTimeout(() => {

          onUploadComplete(newDoc, extractedText);

          router.push(`/editor?documentId=${encodeURIComponent(newDoc.id)}`);

        }, 1000);

        

    } catch(e) {

        console.error("Failed to process file:", e);

        setError("Could not read or process the file. Please ensure it's a valid PDF and try again.");

        setIsLoading(false);

    }

  };



  if (isLoading) {

      return (
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
            className="flex flex-col items-center justify-center h-screen bg-gradient-to-br from-indigo-950 via-slate-900 to-black text-white text-center p-4"
          >
            <Card className="glassmorphism p-8 rounded-xl shadow-lg flex flex-col items-center">
              <Spinner size="lg" color="white" />
              <h2 className="mt-4 text-2xl font-semibold text-indigo-300">Processing Document...</h2>
              <p className="text-slate-300 mt-2">Finalizing your upload.</p>
            </Card>
          </motion.div>
        );
      }
    
      if (error) {
        return (
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
            className="flex flex-col items-center justify-center h-screen bg-gradient-to-br from-indigo-950 via-slate-900 to-black text-white text-center p-4"
          >
            <Card className="glassmorphism p-8 rounded-xl shadow-lg flex flex-col items-center text-red-400">
              <FileWarning size={48} className="mb-4" />
              <h2 className="text-2xl font-semibold">Error: {error}</h2>
              <p className="text-slate-300 mt-2">Please go back and try uploading again.</p>
            </Card>
          </motion.div>
        );
      }
    
      return (
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="min-h-screen bg-gradient-to-br from-indigo-950 via-slate-900 to-black p-4 sm:p-6 lg:p-8 flex items-center justify-center"
        >
          <div className="max-w-4xl w-full glassmorphism p-8 rounded-xl shadow-lg">
            <h1 className="text-3xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-indigo-400 to-slate-200 mb-7 text-center">Upload Document</h1>
            {error && (
              <motion.div
                initial={{ opacity: 0, y: -20 }}
                animate={{ opacity: 1, y: 0 }}
                className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded-xl relative mb-6 dark:bg-red-900 dark:text-red-200"
                role="alert"
              >
                {error}
              </motion.div>
            )}

            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {/* Project Name */}
                <Input
                  label="Project Name"
                  value={projectName}
                  onValueChange={setProjectName}
                  placeholder="e.g., Q3 Marketing Campaign"
                  variant="bordered"
                  fullWidth
                  isRequired
                  classNames={{
                    inputWrapper: "bg-slate-800/50 group-data-[focused=true]:border-indigo-500",
                    label: "text-slate-300 group-data-[focused=true]:text-indigo-400",
                    input: "text-white",
                  }}
                />

                {/* Document Type */}
                <Select
                  label="Document Type"
                  selectedKeys={[documentType]}
                  onSelectionChange={(keys) => setDocumentType(Array.from(keys).join(""))}
                  variant="bordered"
                  fullWidth
                  isRequired
                  classNames={{
                    trigger: "bg-slate-800/50",
                    label: "text-slate-300 group-data-[focused=true]:text-indigo-400",
                    value: "text-white",
                  }}
                >
                  {DOCUMENT_TYPES.map((type) => (
                    <SelectItem key={type} value={type}>
                      {type}
                    </SelectItem>
                  ))}
                </Select>

                {/* Title of the Piece (Optional) */}
                <Input
                  label="Title of the Piece (Optional)"
                  value={title}
                  onValueChange={setTitle}
                  placeholder="Defaults to filename if left blank"
                  variant="bordered"
                  fullWidth
                  classNames={{
                    inputWrapper: "bg-slate-800/50 group-data-[focused=true]:border-indigo-500",
                    label: "text-slate-300 group-data-[focused=true]:text-indigo-400",
                    input: "text-white",
                  }}
                  className="md:col-span-2"
                />

                {/* Anticipated First Use Date */}
                <Input
                  type="date"
                  label="Anticipated First Use Date"
                  value={firstUseDate}
                  onValueChange={setFirstUseDate}
                  variant="bordered"
                  fullWidth
                  isRequired
                  classNames={{
                    inputWrapper: "bg-slate-800/50 group-data-[focused=true]:border-indigo-500",
                    label: "text-slate-300 group-data-[focused=true]:text-indigo-400",
                    input: "text-white",
                  }}
                />

                {/* As of Content Date */}
                <Input
                  type="date"
                  label="As of Content Date"
                  value={asOfContentDate}
                  onValueChange={setAsOfContentDate}
                  variant="bordered"
                  fullWidth
                  isRequired
                  classNames={{
                    inputWrapper: "bg-slate-800/50 group-data-[focused=true]:border-indigo-500",
                    label: "text-slate-300 group-data-[focused=true]:text-indigo-400",
                    input: "text-white",
                  }}
                />

                {/* Production Frequency */}
                <Select
                  label="Production Frequency"
                  selectedKeys={[productionFrequency]}
                  onSelectionChange={(keys) => setProductionFrequency(Array.from(keys).join(""))}
                  variant="bordered"
                  fullWidth
                  isRequired
                  classNames={{
                    trigger: "bg-slate-800/50",
                    label: "text-slate-300 group-data-[focused=true]:text-indigo-400",
                    value: "text-white",
                  }}
                >
                  {PRODUCTION_FREQUENCIES.map((freq) => (
                    <SelectItem key={freq} value={freq}>
                      {freq}
                    </SelectItem>
                  ))}
                </Select>

                {/* Target Audience */}
                <Select
                  label="Target Audience"
                  selectedKeys={[targetAudience]}
                  onSelectionChange={(keys) => setTargetAudience(Array.from(keys).join(""))}
                  variant="bordered"
                  fullWidth
                  isRequired
                  classNames={{
                    trigger: "bg-slate-800/50",
                    label: "text-slate-300 group-data-[focused=true]:text-indigo-400",
                    value: "text-white",
                  }}
                >
                  {TARGET_AUDIENCES.map((audience) => (
                    <SelectItem key={audience} value={audience}>
                      {audience}
                    </SelectItem>
                  ))}
                </Select>

                {/* Is a product mentioned? */}
                <RadioGroup
                  label="Is a product mentioned?"
                  orientation="horizontal"
                  value={isProductMentioned ? "yes" : "no"}
                  onValueChange={(value) => setIsProductMentioned(value === "yes")}
                  className="md:col-span-2"
                  classNames={{
                    label: "text-slate-300",
                  }}
                >
                  <Radio value="yes" classNames={{ label: "text-white" }}>Yes</Radio>
                  <Radio value="no" classNames={{ label: "text-white" }}>No</Radio>
                </RadioGroup>

                {/* Fund Type (Conditional) */}
                {isProductMentioned && (
                  <Select
                    label="Fund Type"
                    selectedKeys={[fundType]}
                    onSelectionChange={(keys) => setFundType(Array.from(keys).join(""))}
                    variant="bordered"
                    fullWidth
                    isRequired
                    className="md:col-span-2"
                    classNames={{
                      trigger: "bg-slate-800/50",
                      label: "text-slate-300 group-data-[focused=true]:text-indigo-400",
                      value: "text-white",
                    }}
                  >
                    {FUND_TYPES.map((type) => (
                      <SelectItem key={type} value={type}>
                        {type}
                      </SelectItem>
                    ))}
                  </Select>
                )}

                {/* Document File */}
                <div className="md:col-span-2">
                  <label className="block text-sm font-medium text-slate-300 mb-1">Document File</label>
                  <motion.div
                    whileHover={{ scale: 1.01 }}
                    className="mt-1 flex justify-center px-6 pt-5 pb-6 border-2 border-indigo-400 dark:border-indigo-600 border-dashed rounded-xl cursor-pointer transition-colors duration-200 hover:border-indigo-500 dark:hover:border-indigo-400 bg-slate-800/20"
                  >
                    <div className="space-y-1 text-center">
                      <UploadCloud className="mx-auto h-12 w-12 text-indigo-400 dark:text-indigo-500" />
                      <div className="flex text-sm text-slate-300">
                        <label
                          htmlFor="file-upload"
                          className="relative cursor-pointer rounded-md font-medium text-indigo-400 hover:text-indigo-300 focus-within:outline-none focus-within:ring-2 focus-within:ring-offset-2 focus-within:ring-indigo-500 dark:text-indigo-400 dark:hover:text-indigo-300"
                        >
                          <span>Upload a file</span>
                          <input
                            id="file-upload"
                            name="file-upload"
                            type="file"
                            className="sr-only"
                            onChange={e => setFile(e.target.files ? e.target.files[0] : null)}
                            accept="application/pdf" // Accept only PDF files
                          />
                        </label>
                        <p className="pl-1">or drag and drop</p>
                      </div>
                      <p className="text-xs text-slate-400">PDF files only, up to 10MB</p>
                      {file && <p className="text-sm text-green-400 mt-2">{file.name}</p>}
                    </div>
                  </motion.div>
                </div>
              </div>
              <div className="flex justify-end space-x-4 pt-6">
                <Button
                  as={motion.button}
                  type="button"
                  onPress={() => router.push('/dashboard')}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  className="bg-slate-700 text-white shadow-lg"
                >
                  Cancel
                </Button>
                <Button
                  as={motion.button}
                  type="submit"
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  className="bg-gradient-to-tr from-indigo-500 to-purple-600 text-white shadow-lg"
                >
                  Upload File
                </Button>
              </div>
            </form>
          </div>
        </motion.div>
      );
    };
    
    export default UploadPage;
